import React from "react";
import classes from './Post.module.css';


const Post = (props) => {
    return <div className={classes.item}>
        <div className={classes.user}>
        <img
            src="data:image/jpeg;base64,"
            alt=""/>
        <div className={classes.text}>
            { props.message}
        </div>
        </div>

        <div>
            <span><img src="https://w7.pngwing.com/pngs/893/45/png-transparent-heart-love-one-like-instagram-ui-twotone-icon-thumbnail.png" alt=""/></span>{props.numberOfLikes}
        </div>
    </div>
}
export default Post;